/* You can add global styles to this file, and also import other style files */
body {
  overflow: hidden;
}
.name{
  font-size: 1.5em;
  color:rgb(218, 218, 60);
}
.service{
  font-size: 1.1em;
}
.tooltip{
  position: absolute;
  padding: 10px;
  z-index: 10;
  min-width: 150px;
  min-height:100px;
  background-color:rgba(32, 32, 60, 0.8);
  color:silver;
  border-radius:5px;
  opacity:0;
  transition:opacity 0.2s linear;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.RRs{

  cursor: pointer;
  width: 150px;
  height: 10%;
  transition: .3s;
  margin-bottom: 20px;
  float: right;
}
.RRs:before{
  content: "Requests";
  color: rgb(218, 218, 60);
  transition: .3s;
}
.RRs::after{
  content: "/Responses";
  transition: .3s;
}
/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch{
  direction: rtl;
  display: flex;
  position:fixed;
}
.chevron{
  position: relative;
  left: 370px;
  cursor:pointer;
  margin-top: 1;
  filter: invert(100%) sepia(0%) saturate(3537%) hue-rotate(355deg) brightness(90%) contrast(83%);
  transition: transform cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s;

}
.chevron.up{
  transform: rotate(180deg);
}
.chevron:hover{
  filter: invert(100%) sepia(1%) saturate(198%) hue-rotate(228deg) brightness(115%) contrast(92%);
  transform: rotate(180deg);

}
.chevron.up:hover{
  transform: rotate(0deg);
}
@media only screen and (min-width: 1000px) {
input:checked + .RRs:after {
  color: rgb(218, 218, 60);
  content: "Responses";
  transition: .3s;
}
input:checked + .RRs:before {
  color: silver;
  content: "Requests/";
  transition: .3s;
}
input[type=range] {
  height: 21px;
  margin: 10px 5% 10px 5%;
  width: 90%;
  background: none;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100px;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000;
  background: #1D7F82;
  border-radius: 4px;
  border: 0px solid #223823;
}
input[type=range]::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000000;
  border: 1px solid #000000;
  height: 13px;
  width: 45px;
  border-radius: 41px;
  background: #E0E6DF;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -5.5px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #1D7F82;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000;
  background: #9e0e0e;
  border-radius: 4px;
  border: 0px solid #223823;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000;
  border: 1px solid #000000;
  height: 13px;
  width: 45px;
  border-radius: 41px;
  background: #E0E6DF;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 3px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #1D7F82;
  border: 0px solid #223823;
  border-radius: 8px;
  box-shadow: 1px 1px 1px #000000;
}
input[type=range]::-ms-fill-upper {
  background: #1D7F82;
  border: 0px solid #223823;
  border-radius: 8px;
  box-shadow: 1px 1px 1px #000000;
}
input[type=range]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 1px 1px 1px #000000;
  border: 1px solid #000000;
  height: 13px;
  width: 45px;
  border-radius: 41px;
  background: #E0E6DF;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: #1D7F82;
}
input[type=range]:focus::-ms-fill-upper {
  background: #1D7F82;
}
.play{
  cursor:pointer;
  margin-top: 1;
  filter: invert(100%) sepia(0%) saturate(3537%) hue-rotate(355deg) brightness(90%) contrast(83%);
}
.play:hover{
  filter: invert(100%) sepia(1%) saturate(198%) hue-rotate(228deg) brightness(115%) contrast(92%);
}

.card{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  position:absolute;
  z-index: 10;
  background-color:rgba(24, 24, 24, 0.829);
  height: 400px;
  width: 500px;
  left:50vw;
  top:calc(100vh - 40px) ;
  border-radius: 5px;
  border:solid white;
  transition: top cubic-bezier(0.075, 0.82, 0.165, 1) 0.3s;

}
.card .pad{
  color:white;
  font-size: 20px;
  padding: 5px 10px 5px 10px;
  border-bottom: solid 1px white;
  background: #000000a4;
}
.card .section{
  color: white;
  font-size: 20px;
  padding: 5px 10px 5px 10px;
  border-bottom: solid 1px white;
  margin: 0;
}
.card.up{
top:calc(100vh - 400px);
}
}
